.block {
  display: flex;
  flex-direction: column;
  padding: 0 20 20 20;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  width: 500px;
  background: #fef4f2;
  border-radius: 10px;
  box-shadow: rgba(255, 145, 249, 0.2) 0px 8px 24px;
}

.h {
  margin-bottom: 20px;
  font-size: 22px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bold;
  color: #28284a;
}
